.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    top: 0;
    z-index: 100;
    background-color: white;
    height: 40px;
	  position: sticky;
  }

  .header__left:hover{
    cursor: pointer;
  }
  
  .header__logo {
    height: 25px;
    object-fit: contain;
    margin-left: 20px;
  }

  .header__input{
      display: flex;
      align-items: center;
      width: 40%;
      border: 1px solid lightgray;
      margin-right: 5px;
  }

  .header__inputButton{
      width: 50px !important;
      background-color: #fafafa;
      border-left: 1px solid lightgray;
      color: gray
  }

  .header__icons{
    display: flex;
    align-items: center;
  }

  .header__icon{
    margin-right: 5px;
    margin-left: 15px;
    cursor: pointer;
  }

  .header__input > input {
    flex: 1;
    border: none;
    outline: none;
  }