.channelRow{
    display: flex;
    align-items: center;
    width: 70%;
}

.channelRow__logo{
    height: 120px !important;
    width: 120px !important;
    margin: 10px 60px;
}

.channelRow__text{
    display: flex;
    flex-direction: column;
}

.channelRow__text > h4{
    display: flex;
    align-items:center;
    margin-bottom: 0px;
    margin-top: 10px;
    
}

.channelRow__text > p{
    color: #606060;
    font-size: small !important;
    margin-bottom: 0px;
    margin-top: 10px;
}

.channelRow__verifyed{
    height: 15px !important;
    width: 15px !important;
    margin-left: 5px;
    color: gray;
}